<template>
	<div class="bookDetail">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/loginSuccess/book' }">
        <i class="el-icon-arrow-left"></i>
      </el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/loginSuccess/homepage' }"></el-breadcrumb-item>
      <el-breadcrumb-item>/</el-breadcrumb-item>
    </el-breadcrumb>
		<el-row :gutter="20">
          <el-col :span="6" class="basic-info">
              <el-col :span="24" class="user-img">
                <img :src="Koala" alt="" >
                <el-form :inline="true" :model="formInline" class="demo-form-inline">
        				  <el-form-item>
        				    <span v-text="name"></span>
        				  </el-form-item>
        				</el-form>

            		<el-form ref="form" :model="form" class="user-basic-info" label-width="80px">
                  <el-form-item label="">
                  	<span v-text="form.sex"></span>
                  </el-form-item>
                  <el-form-item label="">
                  	<span v-text="form.age"></span>
                  </el-form-item>
            		  <el-form-item label="">
            		  	<span v-text="form.phone"></span>
            		  </el-form-item>
            		</el-form>
              </el-col>

              <el-col :span="24" class="body-info">
                  <h2> <el-button size="small" v-if="!editable" @click="editable = true" style="float:right;" icon="edit"></el-button>
                  </h2>

                  <div class="user-info-form" v-if="editable">
                  	<el-form ref="form" :model="newInfo" label-width="110px">
                      <el-form-item label="">
                        <el-input v-model="newInfo.height"></el-input>
                      </el-form-item>
                      <el-form-item label="">
                        <el-input v-model="newInfo.weight"></el-input>
                      </el-form-item>
                      <el-form-item label="">
                        <el-input v-model="newInfo.temperature"></el-input>
                      </el-form-item>
                      <el-form-item label="">
                        <el-input v-model="newInfo.bp"></el-input>
                      </el-form-item>
                      <el-form-item label="">
                        <el-input v-model="newInfo.hr"></el-input>
                      </el-form-item>
                      <el-form-item class="special">
                        <el-button type="primary" @click="submitForm()"></el-button>
                        <el-button @click="resetForm()"></el-button>
                      </el-form-item>
                  	</el-form> 
                  </div>

                  <div class="user-info-text" v-else>
                    <el-form label-position="right" label-width="110px" :model="newInfo">
                      <el-form-item label="">
                        {{ newInfo.height }}
                      </el-form-item>

                      <el-form-item label="">
                        {{ newInfo.weight }}
                      </el-form-item>

                      <el-form-item label="">
                        {{ newInfo.temperature }}    
                      </el-form-item>

                      <el-form-item label="">
                        {{ newInfo.bp }} 
                      </el-form-item>

                      <el-form-item label="">
                        {{ newInfo.hr }}
                      </el-form-item>   
                    </el-form>
                  </div>
          	   </el-col>
          </el-col>

                <el-col :span="18" class="bookDetailRight">
                  <el-col :span="24" class="reserveInformation">
                      <h2></h2>
                      <div class="bookInfo">
                        <el-form ref="form" :model="bookInfos" label-width="110px">
                          <el-form-item label="">
                            <span v-text="bookInfos.orderNum"></span>
                          </el-form-item>
                          <el-form-item label="">
                            <span v-text="bookInfos.ofTime"></span>
                          </el-form-item>
                          <el-form-item label="">
                            <span v-text="bookInfos.bookingChannels"></span>
                          </el-form-item>
                          <el-form-item label="">
                            <span v-text="bookInfos.saleChannels"></span>
                            <el-button type="text" icon="document" size="small" style="float:right;"></el-button>
                          </el-form-item>
                        </el-form> 
                      </div>
                  </el-col>

                  <el-col :span="24" class="inquiryInformation">
    	            	<h2></h2>
	      		        <div class="inquiryInfo">
                        <el-form ref="form" :model="inquiryInfo" label-width="110px">
                          <el-form-item label="">
                            <span v-text="inquiryInfo.clinicTime"></span>
                          </el-form-item>
                          <el-form-item label="">
                            <span v-text="inquiryInfo.clinicType"></span>
                          </el-form-item>
                          <el-form-item label="">
                            <span v-text="inquiryInfo.clinicDoctor"></span>
                          </el-form-item>
                          <el-form-item label="">
                            <span v-text="inquiryInfo.situation"></span>
                          </el-form-item>
                        </el-form> 
                      </div>
                  </el-col>

                  <el-col :span="24" class="operateButton">
                    <el-button type="success" @click="Modificationtime"></el-button>
                    <el-button type="success" @click="Modificationdoctor"></el-button>
                    <el-button type="default" @click="cancel"></el-button>
                  </el-col>
                  <el-dialog class="Modificationtimeform" title="" :visible.sync="timeFormVisible">
                    <el-form ref="form" :model="alterTimeform" label-width="110px">
                          <el-form-item label="">
                            <span v-text="alterTimeform.patient"></span>
                          </el-form-item>
                          <el-form-item label="">
                            <el-date-picker v-model="alterTimeform.value1" type="date" placeholder="">
                            </el-date-picker>
                            <el-time-picker is-range v-model="alterTimeform.value2" placeholder="">
                            </el-time-picker>
                          </el-form-item>
                          <el-col :span="24" class="operateButton">
                          <el-button @click="timeFormVisible = false"></el-button>
                          <el-button type="success" @click="submitTable1()"></el-button>
                          </el-col>
                        </el-form>
                  </el-dialog>
                  <el-dialog class="Modificationdoctorform" title="" :visible.sync="doctorFormVisible">
                    <el-form ref="form" :model="alterDoctorform" label-width="110px">
                          <el-form-item label="">
                            <span v-text="alterDoctorform.patient"></span>
                          </el-form-item>
                          <el-form-item label="">
                            <el-select v-model="alterDoctorform.room" placeholder="">
                              <el-option label="" value="children"></el-option>
                              <el-option label="" value="waike"></el-option>
                            </el-select>
                            <el-select v-model="alterDoctorform.doctor" placeholder="">
                              <el-option label="" value=""></el-option>
                              <el-option label="" value=""></el-option>
                            </el-select>
                          </el-form-item>
                          <el-form-item label="">
                            <el-date-picker v-model="alterDoctorform.value1" type="datetime" placeholder="" :key="alterDoctorform.value1" format="yyyy-MM-dd" @on-change="alterDoctorform.value1=$event">
                            </el-date-picker>
                            <el-time-picker is-range v-model="alterDoctorform.value2" placeholder="">
                            </el-time-picker>
                          </el-form-item>
                          <el-col :span="24" class="operateButton">
                          <el-button @click="doctorFormVisible = false"></el-button>
                          <el-button type="success" @click="submitTable2()"></el-button>
                          </el-col>
                        </el-form>
                  </el-dialog>

                </el-col>
        </el-row>
	</div>
</template>

<script>
  export default {
    data() {
      return {
        timeFormVisible: false,
        doctorFormVisible: false,
        editableT:false,
      	name:'',
        editable:false,
        Koala:"../static/Koala.jpg",
        form:{
          sex:'',
          age:'18',
          phone:'12337657567',
        },
        newInfo:{
        	height:"175",
        	weight:"50kg",
        	temperature:"37",
        	bp:"100",
        	hr:"300"
        },
        bookInfos:{
          orderNum:"pid10000001",
          ofTime:"2016-09-16 16:32:22",
          bookingChannels:"",
          saleChannels:"-"
        },
        inquiryInfo:{
          clinicTime:"2016-09-08",
          clinicType:"",
          clinicDoctor:"",
          situation:""
        },
        alterTimeform:{
          patient:"",
          value1: '',
          value2: [new Date(2016, 9, 10, 8, 40), new Date(2016, 9, 10, 9, 40)],
        },
        alterDoctorform:{
          patient:"",
          room:'',
          doctor:'',
          value1: '',
          value2: [new Date(2016, 9, 10, 8, 40), new Date(2016, 9, 10, 9, 40)],
        }
        
    }
    },
    // computed: {
    //     ...mapGetters({
    //         newInfo_init:'userInfo'
    //     })
    // },
    mounted() {
    var vm = this;
        
        vm.resetForm();
    },
    methods: {
        //
        submitForm() {
            var vm = this;
            vm.editable = false;
            var par = {
                "account":  vm.newInfo.account,
                "height": vm.newInfo.height,
                "weight": vm.newInfo.weight,
                "temperature": vm.newInfo.temperature,
                "dbp": vm.newInfo.dbp,
                "sbp": vm.newInfo.sbp,
            };

            console.log('',par)
        },
        //
        resetForm() {
            var vm = this;
            vm.editable = false;
         //   vm.newInfo = JSON.parse( JSON.stringify(vm.newInfo_init) ) ;
        },
        //
        Modificationtime() {
          this.timeFormVisible = true;
          
        },
        //
        Modificationdoctor() {
          this.doctorFormVisible = true;
          
        },
        //
        submitTable1() {
        var vm = this;
        vm.timeFormVisible = false;
        this.inquiryInfo.clinicTime = this.alterTimeform.value1;
        },
        //
        submitTable2() {
        var vm = this;
        vm.doctorFormVisible = false;
        this.inquiryInfo.clinicDoctor = this.alterDoctorform.doctor;
        },
        //
        handleCreateSubmit(){
            // var vm = this;
            // console.log('',vm.temp,vm.list)

            // // 
            // vm.list.push(vm.temp)
            // console.log('',vm.list)
            this.returnPremiumVisible = false;
        },
  }
}
</script>

<style>
	.bookDetail{
	  	width:82%;
	  	padding:1% 2% 1%;
	  	float:right;
      background: #fff;
	}
  .bookDetail span{
    /*font-weight: bold;*/
    font-size: 14px;
  }

	.user-img,.body-info,.reserveInformation,.inquiryInformation{
		border: 1px solid #dee1e2;
		background: #fff;
		border-radius: 10px;
		margin-bottom: 10px;
	}  
	.basic-info{
        padding: 20px;
        text-align: center;
        /*border: 1px solid #dee1e2;*/
        background: #f6f6f7;
    }
    .basic-info img{
            max-width: 200px;
            max-height: 200px;
            border-radius: 50%;
        }
    .user-basic-info{
    	border-top: 1px solid #dee1e2;
      text-align: initial;
      padding:0 40px !important;
    }
    .body-info{
        padding-bottom: 20px;
        border: 1px solid #dee1e2;
    }
    h2{
        margin: 0;
        font-weight: normal;
        padding: 10px 20px;
        border-bottom: 1px solid #dee1e2;
        .i_edit{
            float: right;
            font-size: 16px;
            color: #7ab8ed;
        }
        }
    .user-info-form .special .el-form-item__content{
        margin-left: inherit !important;
    }

    .user-info-form,.user-info-text{
        padding: 20px;
    }
    .bookDetailRight{
    	   padding: 20px;
        background: #f6f6f7;
    }
    .operateButton{
      text-align: center;
      margin-bottom: 10px;
    }
 </style>


