<template>
    <div class="department">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/loginSuccess/homepage' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/loginSuccess/info' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <div exampleform-row>
        <!-- <el-select v-model="select" slot="prepend" placeholder="" style="width: 130px">
          <el-option v-for="item in typeOptions" :key="item.key" :label="item.display_name" :value="item.key">
          </el-option>
        </el-select> -->
        <el-input v-model="tableData.field1" placeholder=""></el-input>
        
        <el-button type="primary" icon="search" :value="value"></el-button>
        <el-button type="primary" icon="edit" @click="dialogVisible = true"></el-button>
        <el-dialog title="" :visible.sync="dialogVisible" size="tiny" :before-close="handleClose">
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="name">
              <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
              <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item> 
          </el-form> 
        </el-dialog>        
        <el-button type="primary" icon="delete"  @click="removeSelected" :disabled="this.tableData.length === 0"></el-button>
        <el-button type="primary" icon="document"  @click="export2Excel"></el-button>
      </div>

    <!--  -->
      <el-table  v-loading="listLoading" ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%;margin-top:20px;margin-bottom:20px" @selection-change="handleSelectionChange" :label="tableData.num">

      <el-table-column type="selection" width="55" v-model="multipleSelection" @selection-change="handleSelectionChange">
      </el-table-column>
      
      <el-table-column  label="" show-overflow-tooltip>
        <template scope="scope">
                {{scope.$index}}
          </template>
      </el-table-column>

      <el-table-column  label="" show-overflow-tooltip>
        <template scope="scope">
                {{scope.row.field1}}
          </template>
      </el-table-column>


      <el-table-column label="">
        <template scope="scope">
          <el-button size="small" @click="edit(scope.$index,scope)"></el-button>    
          <!-- -->
           <el-dialog title="" v-model="dialogUpdateVisible">
        
               <el-form :model="update" :rules="rule" ref="update" label-width="100px">
                   <el-form-item label="" prop="field1">
                     <el-input v-model="update.field1"></el-input>
                   </el-form-item>
               </el-form>

               <div slot="footer" class="dialog-footer">
                   <el-button @click="dialogUpdateVisible = false"> </el-button>
                   <el-button type="primary" @click="sure()"> </el-button>
               </div>
           </el-dialog>

          <el-button size="small" type="danger" @click="deleteRow(scope.$index,tableData)"></el-button>
        </template>
      </el-table-column>
    </el-table>


    <!--  -->
    <el-form  > 
         <el-col :span="3"><el-form-item style="width: 100px;height:28px;">
             <el-select placeholder="10/">
             <el-option label="10/" value="shi" class="selected"></el-option>
             <el-option label="20/" value="ershi"></el-option>
             <el-option label="30/" value="sanshi"></el-option>
             <el-option label="50/" value="wushi"></el-option>
             </el-select>
         </el-form-item></el-col>
       <el-col :span="3"><div class="block">
       <el-pagination layout="prev, next ,jumper" :total="50"></el-pagination>
       </div></el-col> 
  </el-form>
  </div>
    </div>
</template>

<script type="ecmascript-6">
    export default{
       data() {
        return {
          dialogVisible:false,
          select:'',
          listLoading:false,
          multipleSelection:[],
          value:'',
          // typeOptions:[
          //   { key: '001', display_name: '' },
          //   { key: '002', display_name: '' },
          //   { key: '003', display_name: '' },
          //   { key: '004', display_name: '' }
          // ],
          tableData: [{
            num: '0',
            field1: ''
 
            }, 
            {
              num: '1',
              field1: ''
           
            }, {
              num: '2',
              field1: ''
            
            }, {
              num: '3',
              field1: ''
             
            }, {
               num: '4',
              field1: ''
            
            }, {
               num: '5',
              field1: ''
              
            }, {
              num: '6',
              field1: ''
              
            },
            {
              num: '7',
              field1: ''
              
            },
            {
              num: '8',
              field1: ''
              
            },
            {
              num: '9',
              field1: ''
              
            },
            {
              num: '10',
              field1: ''
              
            },
            {
              num: '11',
              field1: ''
              
            }
          ],
          update:{
              field1: ''
          },
          dialogUpdateVisible: false, //
          multipleSelection: [],
          ruleForm: {
                    name: ''
          },
          rule:'',
          rules: {
            name: [
              { required: true, message: '', trigger: 'blur' },
              { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
            ]
          },
        };
      },
      methods: {
        // 
        deleteRow(index, rows){
          rows.splice(index, 1)
        },
        handleClose(done) {
	        this.$confirm('').then(_ => {
	            done();
	        }).catch(_ => {});
	      },
        // 
	      submitForm(formName) {
          this.$refs[formName].validate((valid) => {
             if (valid) {
             	this.tableData.field1=this.ruleForm.name;
             	// 
             	this.tableData.unshift({field1:this.tableData.field1});
               alert('submit!');
             } else {
               console.log('error submit!!');
               return false;
             }
          });
        },
	      resetForm(formName) {
	        this.$refs[formName].resetFields();
	      },
        // 
        export2Excel() {
          alert(2);
          var vm = this;
        //   require.ensure([], () => {
        //     alert(1);
        //     const { export_json_to_excel } = require('@/vendor/Export2Excel');
        //     const tHeader = [ '', ''];
        //     const filterVal = ['num', 'field1'];
        //     const list = vm.tableData;
        //     const data = vm.formatJson(filterVal, list);
        //     export_json_to_excel(tHeader, data, 'excel');
        //   })
        // },
        // formatJson(filterVal, jsonData) {
        //   return jsonData.map(v => filterVal.map(j => v[j]))
        },
        // 
        handleSelectionChange(val){
              this.multipleSelection = val;   
        },
        removeSelected(){
          for (var i = 0; i < this.multipleSelection.length; i++) {
            this.tableData.splice(this.multipleSelection[i],this.multipleSelection.length);
          }    
        },
        // 
        edit(index, rows){
           this.i=index;
          // this.update.field1=rows.row.field1;
           this.dialogUpdateVisible=true;
        },
        // 
        sure(){       
          var vm  = this;
          vm.dialogUpdateVisible=false;
          this.tableData[this.i].field1 = this.update.field1 ;
        }
    }
  }
</script>

<style rel="stylesheet">
	.department{
	  float:right;
	  width:82%;
    padding:1% 2% 30%;
    background:#fff;
	}
  .department .el-breadcrumb{
    padding:12px;
  }
  .el-input {
      position: relative;
      font-size: 14px;
      float: left;
      width: 120px;
  }

  .department .el-input{
      margin-top: -7px;
      margin-right: 23px;
  }

  .el-select .el-input{
      margin-top: -8px;
  }
   
  .el-input__inner{
    border-radius: 0!important;
    height:28px!important;
    margin:12px;
    float:left;
  }
  .el-pagination{
    padding-top: 4px;
  }
</style>


